import React, { Component } from 'react';
import userData from './api/userData';



class App extends Component {
  constructor(){
    super();
    this.state={
      img:'https://via.placeholder.com/150',
      error:false,
      errorMsg:null,
    }
  }
handleSubmit = (e)=>{
  e.preventDefault();
  userData(this.refs.username.value)
  .then(data=>{
    console.log(data)
    this.setState({
      img:data.hd_profile_pic_url_info.url,
      error:false,
    })
  })
  .catch(err=>{
    console.log('from app.js', err)
    this.setState({error:true, errorMsg: "User Not Found"});
  })
  
}

  render() {
    return (
      <div className="container">
        <div className="row">
      <form onSubmit={this.handleSubmit}>
      <input ref="username"/>
      <button type="submit">send</button>
      </form>
      </div>
      <div className="row">

      {this.state.error?<div>{this.state.errorMsg}</div>:<a target={"_blank"} href={this.state.img}><img alt={"instagram dp"} src={this.state.img}/></a>}
      </div>
      </div>
    );
  }
}

export default App;
